@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

#root {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.mobMenu {
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 4rem;
    width: 16rem;
    height: 21rem;
    background-color: hsl(257, 27%, 26%);
    border-radius: 10px;
    font-weight: 700;
    -webkit-animation: 0.4s FadeIn;
            animation: 0.4s FadeIn;
}

@-webkit-keyframes FadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes FadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.hide {
    visibility: visible;
    opacity: 0;
    max-height: 0px;
    transition: max-height 0.3s, opacity 0.3s, visibility 0.3s linear;
}

.mobMenu ul {
    list-style-type: none;
}

.mobMenu li {
    margin: 1.5rem 0 1.5rem 0;
    text-align: center;
}

.hr {
    width: 14rem;
    height: 1px;
    background-color: hsl(257, 7%, 63%)
}

.sign {
    width: 14rem;
    height: 2.5rem;
    line-height: 2.5rem;
    border: 2px solid hsl(180, 66%, 49%);
    background-color: hsl(180, 66%, 49%);
    border-radius: 2rem;
}
header {
    display: flex;
    justify-content: center;
    width: 100%;
}

.siteNav {
    display: grid;
    width: 100%;
    height: 25rem;
    grid-template-columns: repeat(2,minmax(0,1fr));
    grid-template-rows: 3.5rem 1fr;
    background-image: url(/URLShorteningAPI/static/media/illustration-working.c9b5ef54.svg);
    background-repeat: no-repeat;
    background-size: 380px auto;
    background-position: 1rem;
}

.logo {
    margin-top: 1.5rem;
    margin-left: 1rem;
    width: 80px;
    height: 20px;
}

.menuBar {
    grid-area: 1/2/2/3;
    color: rgb(134, 134, 134);
    justify-self: flex-end;
    margin-top: 1.5rem;
    margin-right: 1rem;
    width: 18px;
    transition: 0.5s;
}

.menuBar:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}


.header {
    display: flex;
    justify-content: center;
  }
  
  .header h2 {
      font-size: 2rem;
      width: 15rem;
      text-align: center;
      color: hsl(257, 27%, 26%);
  }
.siteInfo {
    margin-top: 1rem;
    display: flex;
    justify-content: center;
    margin-bottom: 2rem;
}

.siteInfo p {
    color: hsl(0, 0%, 75%);
    font-size: 0.85rem;
    width: 15rem;
    text-align: center;
}
.mainButton { 
    display: flex;
    justify-content: center;
    align-items: center;
   }
   
   .mainButton button {
     color: white;
     font-weight: 700;
     background-color: hsl(180, 66%, 49%);
     width: 9rem;
     height: 2.5rem;
     border: none;
     border-radius: 2rem;
   }
.shorten {
  z-index: 1;
  background-color: hsl(257, 27%, 26%);
  border-radius: 10px;
  position: absolute;
  top: 21.5rem;
  background-image: url(/URLShorteningAPI/static/media/bg-shorten-mobile.41c34b33.svg);
  background-repeat: no-repeat;
  background-size: 11rem 5rem;
  background-position: top right;
}

.shorten form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 15rem;
  height: 6rem;
}

.shorten input {
  width: 13rem;
  height: 2rem;
  border-radius: 3px;
  border: none;
  outline: none;
  padding-left: 0.3rem;
}

.shorten input::-webkit-input-placeholder {
  color: hsl(0, 0%, 73%);
}

.shorten input:-ms-input-placeholder {
  color: hsl(0, 0%, 73%);
}

.shorten input::placeholder {
  color: hsl(0, 0%, 73%);
}

.shorten button {
  margin-top: 0.5rem;
  width: 13rem;
  height: 2rem;
  border-radius: 3px;
  border: none;
  background-color: hsl(180, 66%, 49%);
  color: white;
}
.fullInfo {
    padding-top: 8rem;
    padding-bottom: 5rem;
    margin-top: 10rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    background-color: #e0e4e6;
  }
  
  .fullInfo h3 {
    color: hsl(260, 8%, 14%);
    text-align: center;
  }
  
  .fullInfo p {
    width: 15rem;
    margin-top: 0.8rem;
    font-size: 0.7rem;
    text-align: center;
    color: hsl(0, 0%, 75%);
  }
  
  .frame {
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    padding-top: 3rem;
    padding-bottom: 1rem;
    background-color: hsl(0, 0%, 100%);
    margin-top: 6rem;
    width: 15rem;
    height: auto;
    border-radius: 5px;
  }
  
  .frame h4 {
    color: hsl(257, 27%, 26%);
  }
  
  .frame p {
    width: 12rem;
  }
  
  .picto {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 8.3rem;
    background-color: hsl(257, 27%, 26%);
    height: 5rem;
    width: 5rem;
    border-radius: 50%;
  }

  .detailed {
    bottom: 9.3rem;
  }

  .picto img {
    width: 3rem;
    height: 3rem;
  }

  .vLine {
    height: 40rem;
    bottom: 20rem;
    position: absolute;
    border: 4px solid hsl(180, 66%, 49%);
    background-color: hsl(180, 66%, 49%);
  }
.back {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 15rem;
    background-color: hsl(257, 27%, 26%);
    color: white;
    background-image: url(/URLShorteningAPI/static/media/bg-boost-mobile.4b04bd89.svg);
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }
  
  .back h3 {
    margin-bottom: 1rem;
  }
.main  {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  position: relative;
}


footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: hsl(260, 8%, 14%);
    width: 100%;
}

footer h2 {
    color: white;
    margin-top: 2rem;
}

footer h4 {
    color: white;
    margin-top: 2rem;
}
footer ul {
    margin-top: 1rem;
    list-style-type: none;
}

footer li {
    font-size: 1rem;
    color: grey;
    text-align: center;
    margin-top: 0.3rem;
}

footer button {
    border: none;
    background-color: transparent;
    color: grey;
    font-size: 1rem;
}

.social {
    display: flex;
    justify-content: space-between;
    width: 12rem;
    margin: 2rem 0 2rem 0;
}

.social img {
    width: 2rem;
}


